import React from 'react';

export default function EventLine(props) {
    let event = props.eventToRender
    return (
        <div className="line row" key={event.id}>
            <div className={"col-md-12 col-lg-7 event-name level-" + event.level}><h6>{event.name}</h6>
            </div>
            <div className="col-md-12 col-lg-5 counters">
                {event.counters.map(c => (
                    <div className="counter"><p>{c}</p></div>
                ))}
            </div>
        </div>
    )
}